<template>
  <div id="app">
    <div
      class="players-left"
      v-if="showMenuModal == false && showGameRulesModal == false && !isMobile"
    >
      <player-card
        class="player"
        v-for="(item, index) in players.playersInfo.length / 2"
        :key="`player-${index}`"
        :player-info="players.playersInfo[index]"
      />
    </div>
    <div class="players" v-if="isMobile">
      <div
        class="players-left"
        v-if="showMenuModal == false && showGameRulesModal == false "
      >
        <player-card
          class="player"
          v-for="(item, index) in players.playersInfo.length / 2"
          :key="`player-${index}`"
          :player-info="players.playersInfo[index]"
        />
      </div>
      <div
        class="players-right"
        v-if="showMenuModal == false && showGameRulesModal == false"
      >
        <player-card
          class="player"
          v-for="(item, index) in players.playersInfo.length / 2"
          :key="`player-${index}`"
          :player-info="
            players.playersInfo[
              players.playersInfo.length == 2 ? index + 1 : index + 2
            ]
          "
        />
      </div>
    </div>

    <board-component
      v-if="showMenuModal == false && showGameRulesModal == false"
      ref="board"
      :player-turn="playerTurn"
      :players="players"
      @pauseGame="showPauseModal = true"
      @restartGame="restartGame()"
      @quitGame="quitGame()"
      @tryAgain="tryAgain()"
      @winner="(winner)=>changeScore(winner)"
      :key="boardKey"
    />
    <div
      class="players-right"
      v-if="showMenuModal == false && showGameRulesModal == false  && !isMobile"
    >
      <player-card
        class="player"
        v-for="(item, index) in players.playersInfo.length / 2"
        :key="`player-${index}`"
        :player-info="
          players.playersInfo[
            players.playersInfo.length == 2 ? index + 1 : index + 2
          ]
        "
      />
    </div>
    <pause-game-modal
      v-if="showPauseModal && showMenuModal == false"
      @continueGame="continueGame()"
      @restartGame="restartGame()"
      @quitGame="quitGame()"
    />
    <menu-component
      v-if="showMenuModal"
      @init="(a) => init(a)"
      @showGameRulesModal="openGameRulesModal()"
    />
    <game-rules-modal
      v-if="showGameRulesModal == true"
      @returntoMenu="closeGameRulesModal()"
    />
    
  </div>
</template>

<script>
import BoardComponent from "./components/BoardComponent.vue";
import PlayerCard from "./components/PlayerCard.vue";
import PauseGameModal from "./components/PauseGameModal.vue";
import MenuComponent from "./components/MenuComponent.vue";
import GameRulesModal from "./components/GameRulesModal.vue";
export default {
  name: "App",
  components: {
    BoardComponent,
    PlayerCard,
    PauseGameModal,
    MenuComponent,
    GameRulesModal,
  },
  data() {
    return {
      playerTurn: 0,
      players: {
        count: 2,
        playersInfo: [
          {
            name: "PLAYER 1",
            score: 0,
            color: "#FD6687",
            hisTurn: true,
            pieces: [],
          },
          {
            name: "PLAYER 2",
            score: 0,
            color: "#52BE80",
            hisTurn: false,
            pieces: [],
          },
        ],
      },
      showPauseModal: false,
      showMenuModal: true,
      showGameRulesModal: false,
      boardKey: 0,
      isMobile: false,
    };
  },
  mounted() {
    this.isMobileDevice();
    window.addEventListener("resize", () => {
      this.isMobileDevice();
    });
  },
  methods: {
    isMobileDevice() {
      if (window.innerWidth <= 1100) {
        this.isMobile = true;
      } else {
        this.isMobile = false;
      }
    },
    init(number) {
      console.log(number);
      if (number == 2) {
        (this.players.count = 2),
          (this.players.playersInfo = [
            {
              name: "PLAYER 1",
              score: 0,
              color: "#FD6687",
              hisTurn: true,
              pieces: [],
            },
            {
              name: "PLAYER 2",
              score: 0,
              color: "#52BE80",
              hisTurn: false,
              pieces: [],
            },
          ]);
        this.showMenuModal = false;
        return;
      }
      (this.players.count = 4),
        (this.players.playersInfo = [
          {
            name: "PLAYER 1",
            score: 0,
            color: "#FD6687",
            hisTurn: true,
            pieces: [],
          },
          {
            name: "PLAYER 2",
            score: 0,
            color: "#52BE80",
            hisTurn: false,
            pieces: [],
          },
          {
            name: "PLAYER 3",
            score: 0,
            color: "#F1C40F",
            hisTurn: true,
            pieces: [],
          },
          {
            name: "PLAYER 4",
            score: 0,
            color: "#884EA0",
            hisTurn: false,
            pieces: [],
          },
        ]);

      this.showMenuModal = false;
    },
    continueGame() {
      this.showPauseModal = false;
      this.$refs["board"].continueGame();
    },
    restartGame() {
      this.showPauseModal = false;
      this.boardKey++;
      //player score reset
      this.players.playersInfo.forEach((player) => {
        player.score = 0;
      });
    },
    quitGame() {
      this.showPauseModal = false;
      this.showMenuModal = true;
    },
    openGameRulesModal() {
      this.showGameRulesModal = true;
      this.showMenuModal = false;
    },
    closeGameRulesModal() {
      this.showGameRulesModal = false;
      this.showMenuModal = true;
    },
    tryAgain() {
      this.showPauseModal = false;
      this.boardKey++;
    },
    changeScore(winner){
      this.players.playersInfo.forEach((player) => {
        if (player.name == winner.name) {
          player.score++;
        }
      });
    
    }
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: auto;
  min-height: 100vh;
  width: 100vw;
  background: #7945ff;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden !important;
  position: relative;
}
.players-left {
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
}
.players-right {
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
}
@media (max-width: 1100px) {
  #app {
    flex-direction: column;
  }
  .players-left,
  .players-right {
    height: auto;
    width: auto;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .players-right {
    order: 0;
  }
  .players {
    display: flex;
    overflow: auto;
    max-width: 100vw;
    padding: 2em 0;
  }
}footer {
  border-radius: 60px 60px 0px 0px;
  background: var(--Dark-Purple, #5c2dd5);
  height: 200px;
  width: 100%;
}
</style>
